import * as vscode from 'vscode';
import { BaseAgent } from './BaseAgent';
import { DevToolsAgent } from './DevToolsAgent';
import { RunAgent } from './RunAgent';

export class AgentRegistry {
  private static instance: AgentRegistry;
  private agents: Map<string, BaseAgent> = new Map();
  private context: vscode.ExtensionContext;

  private constructor(context: vscode.ExtensionContext) {
    this.context = context;
    this.registerBuiltinAgents();
  }

  private registerBuiltinAgents() {
    try {
      // Register core agents
      this.registerAgent(new DevToolsAgent(this.context));
      this.registerAgent(new RunAgent());

      // Conditionally register other agents if available
      try {
        const { ErrorFixAgent } = require('./ErrorFixAgent');
        const { FileManagementAgent } = require('./FileManagementAgent');
        
        this.registerAgent(new ErrorFixAgent());
        this.registerAgent(new FileManagementAgent());
      } catch (error) {
        console.log('Optional agents not available:', error);
      }
    } catch (error) {
      console.error('Error registering built-in agents:', error);
    }
  }

  public static getInstance(context: vscode.ExtensionContext): AgentRegistry {
    if (!AgentRegistry.instance) {
      AgentRegistry.instance = new AgentRegistry(context);
    }
    return AgentRegistry.instance;
  }

  public registerAgent(agent: BaseAgent): void {
    if (this.agents.has(agent.id)) {
      throw new Error(`Agent with ID ${agent.id} is already registered`);
    }
    this.agents.set(agent.id, agent);
  }

  public getAgent(id: string): BaseAgent | undefined {
    return this.agents.get(id);
  }

  public getAllAgents(): BaseAgent[] {
    return Array.from(this.agents.values());
  }

  public findAgentForInput(input: string): BaseAgent | undefined {
    return Array.from(this.agents.values()).find(agent => agent.canHandle(input));
  }
}