import { BaseAgent, AgentContext, AgentResponse } from './BaseAgent';
import * as vscode from 'vscode';

export class RunAgent implements BaseAgent {
  id = 'run-agent';
  name = 'Run Agent';
  description = 'Executes code and commands in the workspace';
  commandPrefix = '@run';

  public canHandle(input: string): boolean {
    return input.startsWith(this.commandPrefix);
  }

  public async execute(context: AgentContext): Promise<AgentResponse> {
    try {
      const { prompt } = context;
      if (!prompt) {
        return {
          success: false,
          message: 'No command provided'
        };
      }

      const command = prompt.substring(this.commandPrefix.length).trim();
      
      // For now, just return the command that would be run
      return {
        success: true,
        message: `Would run command: ${command}`,
        content: command
      };
    } catch (error) {
      return {
        success: false,
        message: `Error executing command: ${error instanceof Error ? error.message : String(error)}`
      };
    }
  }
}
